import server from '../../server.js';
export default {
	data() {
		return {
			shadow:'checked',
			isShowAD:true,
			modalName:''
		}
	},
	methods: {
		kaitong:function(){
			this.modalName=''
			uni.navigateTo({
				url: '../vip/vip'
			})
		},
		userAnquan:function(){
			uni.navigateTo({
				url: '../anquan/anquan'
			});
		},
		logout:function(){
			uni.clearStorage();
			uni.showToast({
				title: "即将退出登录",
				position:'bottom',
				icon:'none',
				position:'center'
			})
			server.userinfo=[];
			server.cookie="";
			setTimeout(function () {
				uni.redirectTo({
					url: '../Home/Home'
				})
			}, 2000);
		},
		SetShadow(e) {
			this.shadow = e.detail.value
			if(e.detail.value==false){
				console.log("先冲会员")
			}
		},
		guanbi:function(){
			
			this.modalName = 'Image'
		},
		getInfo:function(){
			uni.navigateTo({
				url: '../editinfo/editinfo'
			});
		},
		getAbout:function(){
			uni.navigateTo({
				url:'../about/about'
			})
		}
	}
}
